.App-header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: 'Oleo Script Swash Caps', cursive;
  max-height: fit-content;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 0.5rem;
  padding: 0.5rem;
  background-color: antiquewhite;
}

.search-bar > input{
  height: 3rem;
  width:11.5rem;
  padding-left: 1rem;
  padding-right: 1rem;
  border: none;
  border-top-left-radius: 0.5rem;
  border-bottom-left-radius: 0.5rem;
}

input:focus{
  outline-color: lightgray;
  outline-width: 2px;
}

.search-bar{
  display: flex;
}

.search-bar > button{
  height: 3rem;
  width: 3rem;
  border: none;
  border-top-right-radius: 0.5rem;
  border-bottom-right-radius: 0.5rem;  
}

.search-image{
  height: 1rem;
  width: 1rem;
}
thead, tr{
  border: 1px solid black;
}

tr:hover{
  background-color: lightgray;
}
.table-head:hover{
  background-color: white;
}
th, td{
  padding: 0.5rem;
}

table{
  border-collapse: collapse;
  margin-top: 0.5rem;
  width: 90%;
  margin-left: 1rem;
  margin-right: 1rem;
  text-align: center;
}

.loader-img{
  height: 10rem;
  width: 10rem;
}